import { defineStore } from 'pinia'

const defaultTasks = [
  { id: 1, title: '–ü–µ—Ä—à–µ –∑–∞–≤–¥–∞–Ω–Ω—è', description: '–í–º–∏—Ç–∏—Å—è', completed: false },
  { id: 2, title: '–î—Ä—É–≥–µ –∑–∞–≤–¥–∞–Ω–Ω—è', description: '–ü–æ—ó—Å—Ç–∏', completed: false },
  { id: 3, title: '–¢—Ä–µ—Ç—î –∑–∞–≤–¥–∞–Ω–Ω—è', description: '–ü–æ–±–∞–∂–∞—Ç–∏ —Å–æ–±—ñ –≥–∞—Ä–Ω–æ–≥–æ –¥–Ω—è', completed: false }
]


function loadTasks() {
  try {
    const tasks = JSON.parse(localStorage.getItem('tasks'))
    return Array.isArray(tasks) ? tasks : defaultTasks
  } catch (e) {
    console.warn('LocalStorage tasks corrupted, using defaultTasks', e)
    return defaultTasks
  }
}

function saveTasks(tasks) {
  try {
    localStorage.setItem('tasks', JSON.stringify(tasks))
  } catch (e) {
    console.error('Failed to save tasks to LocalStorage', e)
  }
}

export const useTaskStore = defineStore('tasks', {
  state: () => ({
    tasks: loadTasks(),
    filter: 'all'
  }),

  getters: {
    filteredTasks(state) {
      if (state.filter === 'active') return state.tasks.filter(task => !task.completed)
      if (state.filter === 'completed') return state.tasks.filter(task => task.completed)
      return state.tasks
    }
  },

  actions: {
    saveToLocalStorage() {
      saveTasks(this.tasks)
    },

    addTask(task) {
      this.tasks.push(task)
      this.saveToLocalStorage()
    },

    deleteTask(id) {
      this.tasks = this.tasks.filter(task => task.id !== id)
      this.saveToLocalStorage()
    },

    toggleTask(id) {
      const task = this.tasks.find(t => t.id === id)
      if (task) {
        task.completed = !task.completed
        this.saveToLocalStorage()
      }
    },

    updateTask(updatedTask) {
      const index = this.tasks.findIndex(t => t.id === updatedTask.id)
      if (index !== -1) {
        this.tasks[index] = { ...updatedTask }
        this.saveToLocalStorage()
      }
    },

    setFilter(value) {
      this.filter = value
    }
  }
})
